export default  {
        title: 'Users',
        icon: 'mdi-account-multiple',
        filters: [
            {
                input: 'text',
                name: 'displayName',
                bind: {
                    label: "Display Name",
                    prependIcon: 'mdi-account',
                   
                }
            },
            {
                input: 'autocomplete',
                name: 'role',
                bind: {
                    label: "Role",
                    collection: 'role',
                    itemText: "name",
                    itemValue: "role",
                    prependIcon: "mdi-key",
                    
                }
            }, 
            {
                input: 'autocomplete',
                name: 'department',
                bind: {
                    label: "Department",
                    prependIcon: "mdi-briefcase-account-outline",
                    collection: 'department',
                    itemText: "name",
                    itemValue: "_id",
                }
            },
        ],
        table: [
            {
                text: 'Pic',
                align: 'start',
                value: 'profileImage',
            },
            {
              text: 'Display Name',
              align: 'start',
              
              value: 'displayName',
            },
            { text: 'Username', value: 'username' },
            { text: 'Role', value: 'role' },
            { text: 'Business Unit', sortable: false, value: 'business.name' },
            { text: 'Department', sortable: false, value: 'department.name' },
            { text: 'Town', value: 'town' },
            { text: 'Actions', value: 'actions', sortable: false, width: '100' } 
        ],
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'profile-image',
                        name: 'profileImage',
                        bind: {
                            placeholder: "Profile Picture",
                            prependIcon: 'mdi-image',

                        }
                    },
                    {
                        input: 'text',
                        name: 'displayName',
                        bind: {
                            label: "Display Name",
                            prependIcon: 'mdi-account',
                            required: true,
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        } 
                    },
              
                    {
                        input: 'text',
                        name: 'username',
                        bind: {
                            label: "Username",
                            autocomplete: 'username',
                            prependIcon: 'mdi-account-box',
                            required: true,
                            rules: [
                                v => !!v || 'Username is required',
                            ],
                        } 
                    },
                    {
                        input: 'password',
                        name: 'password',
                        bind: {
                            label: "Password",
                            prependIcon: 'mdi-lock',
                             
                            
                        } 
                    },
                    {
                        input: 'select',
                        name: 'town',
                        bind: {
                            label: "Town", 
                            items: ['Haifa', 'Nabless'],
                            prependIcon: "mdi-map-marker",
                            required: true,
                            rules: [
                                v => !!v || 'Town is required',
                            ],
                        } 
                    }, 
                    {
                        input: 'autocomplete',
                        name: 'role',
                        bind: {
                            label: "Role",
                            collection: 'role',
                            itemText: "name",
                            itemValue: "role",
                            prependIcon: "mdi-key",
                            required: true,
                            rules: [
                                v => !!v || 'Role is required',
                            ],
                        }
                    }, 
                    {
                        input: 'autocomplete',
                        name: 'business',
                        default: (state) => state.auth.user.business,
                        if: (state) => !state.auth.user.business,
                        bind: {
                            label: "Business Unit",
                            prependIcon: "mdi-domain",
                            collection: 'business',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'autocomplete',
                        name: 'department',
                        default: (state) => state.auth.user.department,
                        if: (state) => !state.auth.user.department,
                        bind: {
                            label: "Department",
                            prependIcon: "mdi-briefcase-account-outline",
                            collection: 'department',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'autocomplete',
                        if: (state) => ['admin', 'owner'].indexOf(state.auth.user.role) != -1,
                        name: 'affiliate',
                        bind: {
                            label: "Affiliate",
                            prependIcon: "mdi-account-network-outline",
                            collection: 'affiliate',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'switch',
                        name: 'active',
                        default: true,
                        bind: {
                            label: "Active" 
                        } 
                    }
                ]
            }
        ]
    }