export default {
    title: 'Sub-Affiliate',
    icon: 'mdi-account-network-outline',
    table: [
        {
            text: 'Name',
            align: 'start',
            value: 'name',
        },
        {
            text: 'Affiliate',
            value: 'affiliate.name'
        },
        { text: 'Active', value: 'active', sortable: false, width: '10%' },
        { text: 'Actions', value: 'actions', sortable: false, width: '100' }
    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                        rules: [
                            v => !!v || 'Affiliate is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}