import countries from '../country'
import store from './../store'
export default {
    title: 'Customers',
    icon: 'mdi-account-box',
    get assign () {
        return ['admin', 'owner', 'manager'].indexOf(store.state.auth.user.role) !== -1
    } ,
    get select() {
        return ['admin', 'owner', 'manager'].indexOf(store.state.auth.user.role) !== -1
    },
    buttons: [
        {
            text: 'New Deposit',
            icon:'mdi-bank-transfer-in',
            bind: {
                dark:true,
                small: true,
                color: 'green',
            },
            on:(vm) => ({
                click () {
                    const { id } = vm.$route.params
                    vm.$router.replace({ path: '/deposit/new?customer=' + id, query: { backMode: true } })
                } 
            })
        }
    ],
    // import: true,
    // export: true,
    filters:[
        {
            input: 'text',
            name: 'tpc',
            bind: {
                label: "TP Account",
                prependIcon: 'mdi-card-account-details',
             
            }
        },
        {
            input: 'text',
            name: 'name',
            bind: {
                label: "Account Name",
                prependIcon: 'mdi-account-box',
            
            }
        },
        {
            input: 'autocomplete',
            name: 'country',
            bind: {
                label: "Country",
                prependIcon: "mdi-flag",
                items: countries,
                itemText: "name",
                itemValue: "code",
                hideAddNew: true,
                
            }
        },
        {
            input: 'autocomplete',
            name: 'affiliate',
            bind: {
                label: "Affiliate",
                collection: 'affiliate',
                itemText: "name",
                itemValue: "_id",
                prependIcon: "mdi-account-network-outline",
            }
        },
        {
            input: 'autocomplete',
            name: 'user',
            if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),

            bind: {

                label: "Owner",
                prependIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
                or:false,
                query: {
                    role: ['!wakel', '!affiliate']
                }
            }
        },
        {
            input: 'select',
            name: 'status',
            bind: {
                label: "Status",
                items: [
                    { text: 'All', value: null },
                    { text: 'New', value: 'new' },
                    { text: 'Call again', value: 'call again' },
                    { text: 'No Answer', value: 'no Answer' },
                    { text: 'Not interested', value: 'not interested' },
                ],
                itemText: "text",
                itemValue: "value",
                prependIcon: "mdi-list-status",
            }
        }
    ],
    table: [
        {
            text: 'TP Account',
            align: 'start',
            value: 'tpc',
             sortable: false
        },
        { text: 'Account name', value: 'name', sortable: false },
        { text: 'Country', value: 'country', sortable: false },
        { text: 'Affiliate', value: 'affiliate.name', sortable: false },
        { text: 'Subaffiliate', value: 'subaffiliate.name', sortable: false },
        
        { text: 'FTD', value: 'ftd.amount', sortable: false },
        { text: 'FTD Date', value: 'ftd.created_on', width: 120, sortable: false },
        { text: 'Owner Conversion', value: 'ftd.user.displayName', sortable: false },
        
        { text: 'Status', value: 'status', sortable: false },
        { text: 'Owner', value: 'user.displayName', sortable: false },
   
        // { text: 'Deposits', value: 'child.length' },
        // { text: 'Total Deposits', value: 'total[0].amount' },
        { text: 'Actions', value: 'actions', sortable: false, width: '100' }

    ],
    // expanded: {
    //     headers: [
    //         {
    //             text: 'Date',
    //             align: 'start',
    //             value: 'created_on',
    //         },
    //         { text: 'Amount', value: 'amount' },
            
    //     ],
    // },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'tpc',
                    bind: {
                        label: "TP Account",
                        prependIcon: 'mdi-card-account-details',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account Name",
                        prependIcon: 'mdi-account-box',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                        hideAddNew: true,
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                    },
                    on: (vm) => ({
                        init (evt) {
                            if (vm.$refs.customer_subaffiliate) {
                                const [subaffiliate] = vm.$refs.customer_subaffiliate
                                vm.data['subaffiliate'] = null
                                subaffiliate.query.affiliate = evt[0] ? evt[0]._id : '!'
                            }
                        },
                        input (evt) {
                           
                            if (vm.$refs.customer_subaffiliate) {
                                const [subaffiliate] = vm.$refs.customer_subaffiliate
                                vm.data['subaffiliate'] = null 
                                subaffiliate.query.affiliate = evt ? evt : '!'
                            }
                        }
                    }) 
                },
                {
                    input: 'autocomplete',
                    name: 'subaffiliate',
                    default: null,
                    bind: {
                        label: "Subaffiliate",
                        collection: 'subaffiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                        hideAddNew: true,
                        query: { affiliate: '!' }
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                       
                        label: "Owner",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        or: false,
                        query: {
                            role: ['!wakel', '!affiliate'],
                            // department: store.state.rtn
                        },
                        hideAddNew: true,
                    }
                },
                {
                    input: 'select',
                    name: 'status',
                    default: () => 'new',
                    bind: {
                        label: "Status",
                        items: [
                            { text: 'New', value: 'new' },
                            { text: 'Call again', value: 'call again' },
                            { text: 'No Answer', value: 'no Answer' },
                            { text: 'Not interested', value: 'not interested' },
                        ],
                        itemText: "text",
                        itemValue: "value",
                        prependIcon: "mdi-list-status",
                    }
                }
            ]
        },
        {
            label: 'Assignment History',
            if: (vm) => vm.$route.params.id,
            fields: [
                {
                    input: 'assignment',
                    name: 'history',
                    default: () => []
                } 
            ]
        }
    ]
}